<template>
    <div>
        <h2 class="mid">Gallery - Component 1</h2>
        <gallery>
            <div id="gallery" class="gallery">
                <div class="gallery-item" v-for="stuff in loop">
                    <img :src="pickRandomImage()" alt="">
                </div>
            </div>
        </gallery>
    </div>
</template>

<script>
    import {Gallery} from 'vue-detached-scrollbar';

export default {
        data() {
            return{
                loop: 40,
                image: 'http://writm.com/wp-content/uploads/2016/08/Cat-hd-wallpapers.jpg',
                images: [
                    '/static/horse-2574327_640.jpg',
                    '/static/hydrangea-2552663_640.jpg',
                    '/static/tomatoes-2559809_640.jpg',
                    '/static/animal-2572781_640.jpg',
                ],
            };
        },
        methods: {
            pickRandomImage() {
                const min = 0;
                const max = this.images.length;
                const random = Math.floor(Math.random() * (max - min)) + min;
                return this.images[random];
            }
        },
        components: {
            Gallery,
        },
    }
</script>


